header {
  background-color: #fff;
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.25);
  transition: all 0.4s;
  position: relative;
  z-index: 20;
  &.scrolled {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 20;
    .Navigation {
      .nav_top {
        display: none;
      }
      .nav_bot {
        .logo {
          display: none;
        }
      }
    }
  }
  .Navigation {
    max-width: 85%;
    margin: 0 auto;
    .nav_top {
      #user_menu {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        padding: 8px 0;
        position: relative;
        > li {
          > a,
          > span {
            color: #000;
            font-size: 0.85rem;
            padding: 0 16px;
            display: block;
            position: relative;
            &:after {
              content: "";
              width: 1px;
              height: 12px;
              background-color: rgba(0, 0, 0, 0.15);
              position: absolute;
              top: 0;
              bottom: 0;
              margin: auto 0;
              right: 0;
            }
          }
          &:first-of-type {
            a {
              color: #5f0080;
            }
          }
          button {
            font-size: 0.85rem;
            margin-left: 16px;
            cursor: pointer;
            display: flex;
            align-items: center;
            svg {
              margin-left: 6px;
            }
          }
          &:last-of-type {
            .tabmenu {
              display: none;
              position: absolute;
              z-index: 10;
              top: 100%;
              right: 0;
              width: 100px;
              padding: 10px;
              background-color: #fff;
              border: 1px solid rgba(0, 0, 0, 0.55);
              &.tabon {
                display: block;
              }
              li {
                a {
                  font-size: 0.85rem;
                  color: #000;
                  margin-bottom: 10px;
                }
              }
            }
          }
        }
      }
    }
    .nav_bot {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .logo {
        a {
          display: block;
          img {
            display: block;
          }
        }
      }
      .search {
        display: flex;
        align-items: center;
        padding: 10px 20px;
        border: 1px solid #5f0080;
        width: 300px;
        border-radius: 5px;
        input[type="text"] {
          border: none;
          outline: none;
          flex: 1;
          font-size: 16px;
          &::placeholder {
            font-size: 16px;
          }
        }
        button {
          display: flex;
          align-items: center;
          color: #5f0080;
          font-size: 20px;
        }
      }
      .icons {
        > ul {
          display: flex;
          position: relative;
          > li {
            position: relative;
            > button {
              font-size: 26px;
              margin-right: 1rem;
              display: flex;
              align-items: center;
              padding-top: 15px;
              padding-bottom: 15px;
              cursor: pointer;
              &.cart + span {
                position: absolute;
                top: 15px;
                right: 8px;
                background-color: #5f0080;
                color: #fff;
                width: 15px;
                height: 15px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 50%;
              }
            }
            &:first-child {
              &:hover {
                > button + div {
                  display: block;
                }
              }
            }
          }
        }
      }
      .mo_nav {
        display: none;
        > button {
          font-size: 14px;
          letter-spacing: -1px;
          padding: 10px 20px;
          border: 1px solid rgba(0, 0, 0, 0.25);
          box-sizing: border-box;
          border-radius: 20px;
          transition: all 0.4s;
          &:hover {
            background-color: #5f0080;
            border: 1px solid #5f0080;
            color: #fff;
            b {
              color: #fff;
            }
          }
          b {
            color: #5f0080;
          }
        }
        > ul {
          position: fixed;
          background-color: #fff;
          transform: translateX(100%);
          top: 0;
          bottom: 0;
          right: 0;
          z-index: 11;
          transition: transform 0.6s;
          padding: 10px 20px;
          width: 30%;
          &.momenuon {
            transform: translateX(0);
          }
          > li {
            > a {
              color: #333;
              display: block;
              padding: 20px 5px;
              border-bottom: 1px solid rgba(0, 0, 0, 0.25);
              font-weight: 700 !important;
              &:hover {
                color: #5f0080;
              }
            }
            &:first-child {
              text-align: right;
              .close {
                font-size: 20px;
              }
            }
          }
        }
      }
      .mo_search {
        display: none;
        position: relative;
        z-index: 20;
        > button {
          font-size: 30px;
          padding: 20px;
          background-color: #fff;
          box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.25);
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          color: #5f0080;
        }
        > .mosch {
          position: fixed;
          bottom: 0;
          left: 0;
          right: 0;
          background-color: #fff;
          z-index: 11;
          padding: 20px 0;
          transform: translateY(100%);
          transition: transform 0.6s;
          &.schon {
            transform: translateY(0);
          }
          form {
            padding: 10px;
            margin: 10px;
            border: 1px solid #5f0080;
            position: relative;
            input {
              display: block;
              width: 100%;
              border: none;
              outline: none;
            }
            button {
              position: absolute;
              top: 0;
              bottom: 0;
              margin: auto 0;
              right: 0;
              font-size: 18px;
              display: flex;
              align-items: center;
              color: #5f0080;
            }
          }
          button.close {
            position: absolute;
            right: 10px;
            top: 10px;
          }
        }
      }
    }
  }
}

@media (max-width: 1024px) {
  header {
    .Navigation {
      .nav_top {
        #user_menu {
          display: none;
        }
      }
      .nav_bot {
        padding: 16px 0;
        .icons {
          display: none;
        }
        .mo_nav {
          display: block;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  header {
    .Navigation {
      .nav_bot {
        .search {
          display: none;
        }
        .mo_search {
          position: fixed;
          bottom: 30px;
          right: 30px;
          display: block;
        }
        .mo_nav {
          > ul {
            > li {
              > a {
                font-size: 14px;
                padding: 15px 5px;
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 560px) {
  header {
    .Navigation {
      .nav_bot {
        .mo_nav {
          > ul {
            width: 60%;
            > li {
              > a {
                font-size: 12px;
                padding: 12px 5px;
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 460px) {
  header {
    .Navigation {
      .nav_bot {
        .logo {
          a {
            img {
              width: 60px;
            }
          }
        }
        .mo_nav {
          > button {
            font-size: 12px;
            padding: 5px 15px;
          }
        }
      }
    }
  }
}
